.ContractGroupPage-loading .ContractGroupPage-GroupProfile {
  display: none; }

.ContractGroupPage-loading .Spinner {
  display: flex;
  height: calc(100vh * .3); }

.ContractGroupPage-chart {
  margin-bottom: 15px;
  cursor: pointer; }

.ContractGroupPage-ContractGroup .TopContractsCharts-chart {
  height: auto; }

.ContractGroupPage-ContractGroup-header {
  font-size: 1.5em;
  text-align: center;
  padding: 15px 0; }

.ContractGroupPage-GroupProfile {
  padding-top: 10px; }
  .ContractGroupPage-GroupProfile-logoContainer {
    display: flex;
    justify-content: center;
    margin-top: -100px; }
    @media only screen and (max-width: 850px) {
      .ContractGroupPage-GroupProfile-logoContainer {
        margin-top: -50px; } }
  .ContractGroupPage-GroupProfile-logo {
    border-radius: 50%;
    border: 2px solid black; }
    @media only screen and (max-width: 850px) {
      .ContractGroupPage-GroupProfile-logo {
        height: 100px;
        width: 100px; } }
  .ContractGroupPage-GroupProfile-banner {
    width: calc(100% + 40px);
    margin-left: -20px; }
  .ContractGroupPage-GroupProfile-bioAndUrl {
    display: flex;
    justify-content: space-between;
    margin-top: -87px;
    margin-bottom: 57px; }
    @media only screen and (max-width: 850px) {
      .ContractGroupPage-GroupProfile-bioAndUrl {
        margin-top: -43px;
        margin-bottom: 11px; } }
    @media only screen and (max-width: 650px) {
      .ContractGroupPage-GroupProfile-bioAndUrl {
        margin-top: 0;
        margin-bottom: 0;
        flex-direction: column;
        align-items: center;
        text-align: center; } }
    .ContractGroupPage-GroupProfile-bioAndUrl > div {
      width: 300px; }
      @media only screen and (max-width: 650px) {
        .ContractGroupPage-GroupProfile-bioAndUrl > div {
          width: 100%;
          margin: 5px 0; } }
