.Dropdown {
  width: fit-content;
  padding: 5px 20px 5px 10px;
  border-radius: 4px;
  color: white;
  background-image: url(data:image/svg+xml;base64,);
  background-position-y: center;
  background-position-x: calc(100% - 5px);
  background-repeat: no-repeat;
  background-color: #182024;
  -webkit-appearance: none;
  appearance: none;
  height: 35px; }
  .Dropdown::-ms-expand {
    display: none; }
  .Dropdown-fullWidth {
    width: 100%; }
  .Dropdown-noIcon {
    background: none;
    padding-right: 5px; }
  .Dropdown:focus {
    outline: none; }
  .Dropdown-disabled {
    cursor: not-allowed;
    border-color: grey;
    background-color: gret; }
    .Dropdown-disabled input {
      color: #606060;
      -webkit-text-fill-color: #606060;
      opacity: 1;
      cursor: not-allowed; }
