.PageModal-backButton {
  position: absolute;
  top: 0px;
  left: -3px;
  color: #a1a1aa; }

.PageModal-prices {
  text-align: center;
  font-size: 13px;
  color: #a1a1aa;
  margin: -10px 0 0; }
  .PageModal-prices > span:not(:last-child) {
    margin-right: 10px; }
  .PageModal-prices-price {
    color: white;
    margin-left: 5px;
    opacity: .8; }
  .PageModal-prices-middle {
    font-size: 15px; }
    .PageModal-prices-middle-price {
      opacity: 1;
      color: #e7d6b5; }

.PageModal-avgBurn {
  margin-top: 10px;
  text-align: center; }
  .PageModal-avgBurn span {
    color: #e7d6b5; }

.PageModal .Modal-window {
  max-width: 1200px; }
  @media only screen and (max-width: 721px) {
    .PageModal .Modal-window {
      padding: 50px 10px 10px;
      border: none;
      border-radius: 0;
      max-height: -webkit-fill-available;
      margin: 0;
      height: 100%; } }

.PageModal-sliderRow {
  display: flex;
  align-items: center;
  justify-content: center; }
  .PageModal-sliderRow .Checkbox {
    margin-left: 10px; }

.PageModal .Dropdown {
  margin: 10px 0;
  min-height: 29px; }

.PageModal-donate {
  text-align: left;
  color: #a1a1aa;
  font-size: 14px;
  cursor: pointer;
  margin-top: 20px;
  display: flex; }
  .PageModal-donate .Icon {
    margin-left: -3px; }
  @media only screen and (max-width: 600px) {
    .PageModal-donate {
      margin-left: 10px; } }

.PageModal-address {
  margin-top: 10px;
  display: flex;
  background: #373737;
  border-radius: 4px; }
  .PageModal-address input {
    color: #b3c0f9;
    background: #373737;
    border: none;
    text-overflow: ellipsis;
    width: 100%;
    padding: 10px 5px 10px 10px;
    border-radius: 4px; }
    @media only screen and (min-width: 475px) {
      .PageModal-address input {
        width: 340px; } }
  .PageModal-address .CopyButton {
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-right: 10px;
    padding-left: 10px;
    border-left: 1px solid grey; }
    .PageModal-address .CopyButton img {
      height: 25px;
      width: 25px; }

.PageModal-currencyMessage {
  margin-top: 10px; }

.PageModal-qrCode {
  margin-top: 20px;
  margin-bottom: 10px;
  width: 150px;
  height: 150px; }

.PageModal-donateWrapper {
  display: flex;
  flex-direction: column;
  align-items: center; }

.Slider {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
  margin-bottom: 10px; }
  .Slider > label {
    margin-left: 10px; }
